<template>
  <div>
    <div>
      <b-form-group label="Adresse" label-for="login-otp">
        <b-form-input
          id="otp"
          v-model="adresse"
          name="otp"
          placeholder="Adresse"
          @keyup="change_adresse()"
        />
      </b-form-group>
    </div>
    <div id="get-adresse">
      <div :key="index" v-for="(data, index) in find_data">
        <p>{{ data.Description }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import {
  BCard,
  BCardText,
  BTable,
  BAvatar,
  BBadge,
  BRow,
  BCol,
  BFormGroup,
  BFormSelect,
  BPagination,
  BInputGroup,
  BFormInput,
  BInputGroupAppend,
  BButton,
  BDropdown,
  BDropdownItem,
  BModal,
  VBModal,
  BForm,
  BLink,
} from "bootstrap-vue";

export default {
  components: {
    BCard,
    BCardText,
    BTable,
    BAvatar,
    BBadge,
    BRow,
    BCol,
    BFormGroup,
    BFormSelect,
    BPagination,
    BInputGroup,
    BFormInput,
    BInputGroupAppend,
    BButton,
    BDropdown,
    BDropdownItem,
    BModal,
    BForm,
    BLink,
  },
  data() {
    return {
      loading: false,
      adresse: "",
      find_data: "",
      url: "https://ws1.postescanada-canadapost.ca/addresscomplete/interactive/find/v2.10/json.ws?provider=AddressComplete&package=Interactive&endpoint=json.ws&service=Find&version=2.1&key=TY21-AZ33-ZC68-DU24&Country=CAN&SearchTerm=",
    };
  },
  props: {
    change: Function,
  },
  methods: {
    change_adresse() {
      console.log("change adresse");
      this.$http
        .get(this.url + "" + this.adresse)
        .then((res) => {
          console.log(res.data);
          this.find_data = res.data;
        })
        .catch((errors) => {
          //errors
        });
    },
    get_adresse(adresse) {
      this.adresse = adresse;
      this.change(adresse);
    },
  },
};
</script>

